import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '@/views/HomeView.vue'
import NotFound from '@/views/NotFound.vue'

const routes = [
  {
    path: '/',
    name: 'root',
    component: HomeView,
    meta: {login: false}
  },
  {
    path: '/home',
    name: 'home',
    component: () => import('@/views/Home/IndexView.vue'),
    meta: {title: "Live"},
  },
  {
    path: '/live',
    name: 'live',
    component: () => import('@/views/LiveView.vue'),
    meta: {title: "Live"},
  },
  {
    name: 'notFound',
    path: '/:pathMatch(.*)*',
    component: NotFound,
    meta: {title: "404 Not Found"},
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  // ÊØèÊ¨°ÂàáÊç¢È°µÈù¢Êó∂ÔºåË∞ÉÁî®ËøõÂ∫¶Êù°
  // Ëøô‰∏™‰∏ÄÂÆöË¶ÅÂä†ÔºåÊ≤°Êúânext()È°µÈù¢‰∏ç‰ºöË∑≥ËΩ¨ÁöÑ„ÄÇËøôÈÉ®ÂàÜËøò‰∏çÊ∏ÖÊ•öÁöÑÂéªÁøª‰∏Ä‰∏ãÂÆòÁΩëÂ∞±ÊòéÁôΩ‰∫Ü
  next();
});
router.afterEach((to) => {
  // document.title = "Web | UIM";
  if (to.meta.title) {
    document.title = to.meta.title + " | " + "ÁôΩÊæ™";
  }
}
)

export default router
