<script setup>
import {ref} from "vue";
import axios from "axios";

function NewAddFriend() {
  axios.post(
      "https://api.miochyan.net/website/add/friendlink",
      {
        "url": NewFriend_Link.value,
        "email": NewFriend_Email.value,
        "yfp": NewFriend_YFP.value,
        "phone": NewFriend_QQ.value,
        "title": NewFriend_Name.value,
        "sub": NewFriend_Sub.value,
        "avatar": NewFriend_Avatar.value
      }
  ).then(() => {
    Reload();
    CloseAddFriend();
  })
}




const NewFriend_Email = ref();
const NewFriend_Link = ref();
const NewFriend_YFP = ref();

const NewFriend_QQ = ref();
const NewFriend_Name = ref();
const NewFriend_Avatar = ref();
const NewFriend_Sub = ref();


function isEmail(str) {
  // Ê≠£ÂàôË°®ËææÂºèÔºåÁî®‰∫éÊ£ÄÊµãÁîµÂ≠êÈÇÆ‰ª∂Âú∞ÂùÄÊ†ºÂºè
  let emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

  // ‰ΩøÁî®Ê≠£ÂàôË°®ËææÂºèËøõË°åÂåπÈÖç
  return emailRegex.test(str);
}

function checkURL(URL){
  let str=URL;
  //Âà§Êñ≠URLÂú∞ÂùÄÁöÑÊ≠£ÂàôË°®ËææÂºè‰∏∫:http(s)?://([\w-]+\.)+[\w-]+(/[\w- ./?%&=]*)?
  //‰∏ãÈù¢ÁöÑ‰ª£Á†Å‰∏≠Â∫îÁî®‰∫ÜËΩ¨‰πâÂ≠óÁ¨¶"\"ËæìÂá∫‰∏Ä‰∏™Â≠óÁ¨¶"/"
  let Expression=/(https?:\/\/)(([0-9a-z.]+\.[a-z]+)|(([0-9]{1,3}\.){3}[0-9]{1,3}))(:[0-9]+)?(\/[0-9a-z%/.\-_]*)?(\?[0-9a-z=&%_\-]*)?(\#[0-9a-z=&%_\-]*)?/ig;
  let objExp=new RegExp(Expression);
  if(objExp.test(str)==true){
    return true;
  }else{
    return false;
  }
}


const AddFriendClass = ref("");
function ToAddFriend() {
  AddFriendClass.value = "start";
}
function CloseAddFriend() {
  AddFriendClass.value = "";
}

const FriendLinkData = ref();
function Reload() {
  axios.get(
      "https://api.miochyan.net/website/get/friendlink"
  ).then(response => {
    FriendLinkData.value = response.data;
    }
  )
}
Reload()
</script>