import { h, createApp } from "vue";
import Message from "./msgComp.vue"; // Â∞Ü‰Ω†ÁöÑÈÄöÁü•ÁªÑ‰ª∂‰øùÂ≠ò‰∏∫ÂçïÊñá‰ª∂ÁªÑ‰ª∂

export function sendMsg(targetId, data) {
    const container = document.createElement("div");

    document.getElementById(targetId).appendChild(container);

    const app = createApp({
        render: () => h(Message, { data: data })
    });

    app.mount(container);


}