import { h, createApp } from "vue";
import Message from "./GmsgComp.vue"; // Â∞Ü‰Ω†ÁöÑÈÄöÁü•ÁªÑ‰ª∂‰øùÂ≠ò‰∏∫ÂçïÊñá‰ª∂ÁªÑ‰ª∂

export function gSendMsg(targetId, type, msg, deleteAfterMs = 0) {
    const container = document.createElement("div");

    document.getElementById(targetId).appendChild(container);

    const app = createApp({
        render: () => h(Message, { type: type, deleteAfterMs:deleteAfterMs }, { default: () => msg })
    });

    app.mount(container);


}